/* AntD */

html {
   --main-color: #202020;
   --main-color-light: #303030;
   --bg: linear-gradient(rgba(0, 0, 0, 0), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --bg-dark: linear-gradient(rgba(0, 0, 0, 0.25), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --main-font: "Assistant", sans-serif;
   --text-font: "Assistant", sans-serif;
   /* 'Mukta', sans-serif; */
   --number-font: "Stardos Stencil", cursive;
   /* "Special Elite", cursive; */
   --text-font-size: 0.85rem;
   --text-title-size: 1rem;
   margin: 0;
   padding: 0;
   -webkit-touch-callout: none; /* iOS Safari */
   -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
   user-select: none;
   height: 100%;
   width: 100%;
   background-color: #202020;
   background-color: var(--main-color);
}

::-webkit-scrollbar {
   width: 8px;
}
::-webkit-scrollbar-track {
   background: #202020;
   background: var(--main-color);
}
::-webkit-scrollbar-thumb {
   background-color: #454545;
   border-radius: 20px;
}

* {
   box-sizing: border-box;
   margin: 0 0;
}

body {
   background-color: #202020 !important;
   background-color: var(--main-color) !important;
}

.App {
   width: 100%;
   font-family: "Assistant", sans-serif;
   font-family: var(--main-font);
