}

/* Home and Search Spin */
.ant-spin-dot-item {
   background-color: white;
}

.ant-spin-nested-loading {
   width: 100%;
}

.ant-spin-container {
   width: 100%;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
}

.ant-spin-container::after {
   background: transparent !important;
}

.ant-spin-blur {
   opacity: 0 !important;
}

.ant-spin-lg .ant-spin-dot {
   margin-top: 15vh !important;
   font-size: 60px !important;
}

.ant-spin-lg .ant-spin-dot i {
   width: 30px !important;
   height: 30px !important;
}

/* animations */
.animation_fade {
   -webkit-animation: animation_fade 1s;
   animation: animation_fade 1s;
}

@-webkit-keyframes animation_fade {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}

@keyframes animation_fade {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}


.Navbar {
   width: 100%;
   height: 90px;
   background: transparent;
   transition: background 0.5s ease-in-out, height 0.5s ease-in-out;
   z-index: 1000;

   display: flex;
   position: fixed;
   list-style: none;
   font-size: 1.1rem;
}

.Navbar .NotDisplay {
   display: none;
}

.Navbar .NavItem {
   transform: translateY(-10vh);
   transition: opacity 2000ms ease, transform 1000ms ease-out;
}

.Navbar .animation_fade_slide_down {
   transform: translateY(0vh);
}

.NavbarActive {
   /* background: #303030d5; */
   transform: translateY(0vh);
   background: #000000;
   height: 70px;
   padding: 0 !important;
}

.Navbar ul {
   height: 100%;
   list-style: none;
   display: inline-block;
   display: flex;
   align-items: stretch;
   -webkit-padding-start: 0px;
           padding-inline-start: 0px;
}

.Navbar ul a,
.Navbar .HoverEffect {
   color: white;
   background-color: transparent;
   transition: background-color 0.5s ease-in-out, color 0.3s ease-in-out;
}

.Navbar ul a:hover,
.Navbar .HoverEffect:hover {
   background-color: white;
   color: black;
}

.Navbar li {
   height: 100%;
   display: flex;
   align-items: center;
   text-decoration: none;
   padding: 0 20px;
   transition: background-color 0.5s ease-in-out, color 0.3s ease-in-out;
}

.NavLeft {
   width: 33%;
}

.SearchBarContainer {
   width: 34%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.SearchBar {
   margin-left: 3%;
   width: 80%;
   max-width: 500px;
   height: 37px;
   align-self: center;
   border: 1px solid white;
   display: flex;
   align-items: center;
}

.SearchBar #SearchInput {
   width: 85%;
   height: 60%;
   background-color: transparent;
   color: white;
   border: none;
}

.SearchBar #SearchInput:focus {
   box-shadow: none;
}

.SearchBar .Icon {
   width: 15%;
