   -webkit-filter: brightness(85%);
           filter: brightness(85%);
   transition: box-shadow 0.5s ease-in-out, transform 0.3s ease-in-out, -webkit-filter 0.5s ease-in-out;
   transition: box-shadow 0.5s ease-in-out, filter 0.5s ease-in-out,
      transform 0.3s ease-in-out;
   transition: box-shadow 0.5s ease-in-out, filter 0.5s ease-in-out,
      transform 0.3s ease-in-out, -webkit-filter 0.5s ease-in-out;

   display: grid;
   grid-template: 3fr 4fr/ 1fr;
   color: black;
   font-family: var(--text-font);
   -webkit-animation: animation_fade 1s;
           animation: animation_fade 1s;
}

.Card:hover {
